*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: montserrat, sans-serif;
}

header{
  background-image: linear-gradient(to right, #FE4880, #00ccff);
  box-shadow: 0px 3px 6px rgba(0,0,0.2);
  padding: 15px;

}
header input{
  display: inline-block;
  color: #FFF;
  font-size: 32px;
  font-weight: 600;
  font-style: italic;
  text-transform: uppercase;
  background: none;
  outline: none;
  border: none;
  appearance: none;
}

header::before{
  content: '/r/';
  display: inline-block;
  color: #FFF;
  font-size: 32px;
  font-weight: 600;
  font-style: italic;
  text-transform: uppercase;
}

.articles article{
  padding: 30px, 15px;
  background-color: #FFF;

}

.articles article a{
  text-decoration: none;
}

.articles article h3{
  color:black;
}

.articles article:nth-child(2n){
  background-color: #fff;
}

.articles article:hover{
  background-image: linear-gradient(to right, #FE4880, #00ccff);
}

Link{
  font-size: 32px;
  font-weight: 600;
  font-style: italic;
  text-transform: uppercase
}
